import { Component, Injector, ViewChild } from '@angular/core';
import { IonContent } from '@ionic/angular';
import { BasePage } from '../base-page/base-page';
import { ReviewAddPage } from '../review-add/review-add';
import { SocialSharing } from '@ionic-native/social-sharing/ngx';
import { Place } from '../../services/place-service';
import { User } from '../../services/user-service';
import { Review } from '../../services/review-service';
import { SignInPage } from '../sign-in/sign-in';
import { Subject, Observable, merge } from 'rxjs';
import { environment } from '../../../environments/environment';
import { DomSanitizer } from '@angular/platform-browser';
import { SharePage } from '../share/share.page';
import { GeolocationService } from 'src/app/services/geolocation.service';
import { GalleryConfig, Gallery } from '@ngx-gallery/core';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';
import { PlaceUserEditPage } from '../place-user-edit/place-user-edit';
import { CheckinPage } from '../checkin/checkin';
import { PlaceMenuPage } from '../place-menu/place-menu.page';

@Component({
  selector: 'page-place-detail',
  templateUrl: 'place-detail.html',
  styleUrls: ['place-detail.scss']
})
export class PlaceDetailPage extends BasePage {

  @ViewChild(IonContent, { static: true }) container: IonContent;

  public apiKey: string = environment.googleMapsApiKey;
  menuActive = 0;
  menuItem: any;
  public images = [];
  public place: Place;
  public description: any;
  public rating: number = 0;
  public isLiked: boolean = false;
  public isStarred: boolean = false;
  public location: any;
  public reviews: Review[] = [];
  public slidesConfig: any = {};
public imageServer : string;
  public contentLoaded: Subject<any>;
  public loadAndScroll: Observable<any>;

  public skeletonImages: Array<any>;
  public skeletonReviews: Array<any>;
  public webSocialShare: { show: boolean, share: any, onClosed: any } = {
    show: false,
    share: {
      config: [{
        facebook: {
          socialShareUrl: '',
        },
      }, {
        twitter: {
          socialShareUrl: '',
        }
      }, {
        whatsapp: {
          socialShareText: '',
          socialShareUrl: '',
        }
      }]
    },
    onClosed: () => {
      this.webSocialShare.show = false;
    }
  };

  public user: User;

  constructor(injector: Injector,
    private placeService: Place,
    private sanitizer: DomSanitizer,
    private geolocationService: GeolocationService,
    private reviewService: Review,
    private gallery: Gallery,
    private socialSharing: SocialSharing) {
    super(injector);

    this.contentLoaded = new Subject();

    this.skeletonImages = Array(6);
    this.skeletonReviews = Array(5);
    this.imageServer = environment.imageServer;
  }

  ngOnInit() {
    this.setupObservable();
    this.setupGallery();


    this.user = User.getCurrent();

    this.events.subscribe('user:login', () => {
      this.user = User.getCurrent();
    });

    this.events.subscribe('user:loggedOut', () => {
      this.user = null;
    });
    this.menuActive;
    this.menuItem;
  }

  async ionViewDidEnter() {
    if (!this.place) {
      this.setupSlider();
      await this.showLoadingView({ showOverlay: false });
      this.loadPlace();
      this.loadLocation();
    } else {
      this.setPageTitle(this.place.title);

      this.setMetaTags({
        title: this.place.title,
        description: this.place.description,
        image: this.place.image ? this.place.image.url() : '',
        slug: this.place.getSlug()
      });
    }
  }

  async openLocalMenu(id: string){

    await this.showLoadingView({ showOverlay: true });

    const modal = await this.modalCtrl.create({
      component: PlaceMenuPage,
      componentProps: {
        pid: id
      }
    });

    await modal.present();

    await this.dismissLoadingView();
  }
  enableMenuSwipe() {
    return false;
  }

  onSlidesDidLoad() {
    this.contentLoaded.next();
  }

  onSlidesDrag() {
    this.contentLoaded.next();
  }
  clickMenu(event) {
    if (event === 0) {
      this.menuActive = 1;
    }
    if (event === 1) {
      this.menuActive = 0;
    }
  }
  changeMenuItem(event) {
    if (this.menuItem === event){
      this.menuItem = null;
    } else {
      this.menuItem = event;
    }
  }
  setupObservable() {
    this.loadAndScroll = merge(
      this.container.ionScroll,
      this.contentLoaded
    );
  }

  setupGallery() {
    const config: GalleryConfig = {
      loadingMode: 'indeterminate'
    };

    const galleryRef = this.gallery.ref('placeGallery');
    galleryRef.setConfig(config)
  }

  onContentLoaded() {
    setTimeout(() => {
      this.contentLoaded.next();
    }, 400);
  }

  async loadPlace() {

    try {

      this.place = await this.placeService.loadOne(this.getParams().id);
      console.log('ksajdflkajsf', this.place);

      if (this.place.longDescription) {
        this.description = this.sanitizer
          .bypassSecurityTrustHtml(this.place.longDescription);
      }

      this.setPageTitle(this.place.title);

      this.setMetaTags({
        title: this.place.title,
        description: this.place.description,
        image: this.place.featuredImageUrl || '',
        slug: this.place.getSlug()
      });

      this.webSocialShare.share.config.forEach((item: any) => {
        if (item.whatsapp) {
          item.whatsapp.socialShareUrl = this.getShareUrl(this.place.getSlug());
        } else if (item.facebook) {
          item.facebook.socialShareUrl = this.getShareUrl(this.place.getSlug());
        } else if (item.twitter) {
          item.twitter.socialShareUrl = this.getShareUrl(this.place.getSlug());
        }
      });

      this.rating = this.place.rating;

      if (User.getCurrent()) {
        this.checkIfIsLiked();
        this.checkIfIsStarred();
      }

      this.loadReviews();
      if(this.place.from === 'app'){
        if (this.place.featuredImageUrl) {
          this.images.push(this.place.featuredImageUrl);
        }

        this.place.images.forEach(element => {
          this.images.push(element._url);
        });
      } else if(this.place.from === 'local'){
        this.place.serverImages.forEach(element => {
          this.images.push(this.imageServer+this.place.reference+'/'+element.url);
        });
      }

      this.showContentView();
      this.onContentLoaded();
      this.onRefreshComplete(this.place);

    } catch (err) {

      if (err.code === 101) {
        this.showEmptyView();
      } else {
        this.showErrorView();
      }

      this.onRefreshComplete();
    }
  }

  async editPlace(event) {
    if (!User.getCurrent()) return this.openSignInModal();

    await this.showLoadingView({ showOverlay: true });

    const modal = await this.modalCtrl.create({
      component: PlaceUserEditPage,
      componentProps: { id: event }
    });

    await modal.present();

    await this.dismissLoadingView();
  }

  setupSlider() {
    this.slidesConfig = {
      grabCursor: true,
      slidesPerView: 2.5,
      breakpointsInverse: true,
      zoom: false,
      touchStartPreventDefault: false,
      breakpoints: {
        // when window width is >= 320px
        320: {
          slidesPerView: 2.2,
          spaceBetween: 6
        },
        // when window width is >= 400px
        400: {
          slidesPerView: 2.5,
          spaceBetween: 5
        },
        // when window width is >= 640px
        600: {
          slidesPerView: 3.5,
          spaceBetween: 10
        },
        800: {
          slidesPerView: 4.5,
          spaceBetween: 10
        }
      }
    };
  }

  async checkIfIsLiked() {
    try {
      const isLiked = await this.placeService.isLiked(this.place)
      this.isLiked = isLiked;
    } catch (err) {
      console.warn(err.message);
    }
  }

  async checkIfIsStarred() {
    try {
      const isStarred = await this.placeService.isStarred(this.place)
      this.isStarred = isStarred;
    } catch (err) {
      console.warn(err.message);
    }
  }

  async loadLocation() {
    try {
      const coords = await this.geolocationService.getCurrentPosition();
      this.location = coords;
    } catch (err) {
      console.warn(err);
    }
  }

  async loadReviews() {
    try {
      this.reviews = await this.reviewService.load({
        place: this.place, limit: 5
      });
    } catch (err) {
      console.warn(err.message);
    }
  }

  async openSignInModal() {

    await this.showLoadingView({ showOverlay: true });

    const modal = await this.modalCtrl.create({
      component: SignInPage
    });

    await modal.present();

    await this.dismissLoadingView();

  }

  async openAddReviewModal() {

    await this.showLoadingView({ showOverlay: true });

    const modal = await this.modalCtrl.create({
      component: ReviewAddPage,
      componentProps: {
        place: this.place
      }
    });

    await modal.present();

    await this.dismissLoadingView();

    const { data } = await modal.onWillDismiss();

    if (data) {
      this.reviews.unshift(data);
    }
  }

  async openCheckInModal() {

    await this.showLoadingView({ showOverlay: true });

    const modal = await this.modalCtrl.create({
      component: CheckinPage,
      componentProps: {
        place: this.place
      }
    });

    await modal.present();

    await this.dismissLoadingView();

    const { data } = await modal.onWillDismiss();

    if (data) {
      this.reviews.unshift(data);
    }
  }

  async openShareModal() {

    await this.showLoadingView({ showOverlay: true });

    const modal = await this.modalCtrl.create({
      component: SharePage,
    });

    await modal.present();

    await this.dismissLoadingView();

  }

  onLike() {

    if (User.getCurrent()) {
      this.isLiked = !this.isLiked;
      this.placeService.like(this.place);
    } else {
      this.openSignInModal();
    }
  }

  onRate() {
    if (User.getCurrent()) {
      this.openAddReviewModal();
    } else {
      this.openSignInModal();
    }
  }

  checkIn() {
    if (User.getCurrent()) {
      this.openCheckInModal();
    } else {
      this.openSignInModal();
    }
  }

  onContentTouched(ev: any = {}) {
    const href = ev.target.getAttribute('href');
    if (href) {
      ev.preventDefault();
      this.openUrl(href);
    }
  }

  async onShare() {

    if (this.isCordova) {

      try {
        const url = this.getShareUrl(this.place.getSlug());
        await this.socialSharing.share(null, null, null, url);
      } catch (err) {
        console.warn(err)
      }

    } else if (this.isPwa || this.isMobile) {
      this.webSocialShare.show = true;
    } else {
      this.openShareModal();
    }

  }

  async onCall() {
    this.openSimpleUrl('tel:' + this.place.phone);
  }

  async onDirectionsButtonTouched() {

    const lat = this.place.location.latitude;
    const lng = this.place.location.longitude;

    const url = `https://maps.google.com/maps?q=${lat},${lng}`;
    //const url = `http://maps.apple.com/?ll=${lat},${lng}`;

    this.openMapUrl(url);
  }

}
